body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


 nav {
  display: flex;
   justify-content: space-between;
   background-color: #C3DEE8;
   align-items: center;
  } 
  .navheading {
      margin-left: 15px;
      font-size: 1.5em;
      padding: 0 10px 10px 10px;
      color: #020259;
      font-style: italic;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
 nav ul { 
    display: flex; 
    margin: 0;
    list-style: none;    
  }
 nav ul li {
      display: inline-block;
      margin-right: 8px;
      opacity: 0.6;
      padding: 0 10px 20px 10px;
      font-size: 1em;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
nav ul li a {
  display: block;
  padding: 15px;
  text-decoration: none;
  color: #A67C00;

  margin: 0 10px;
}
nav ul li a,
nav ul li a:after,
nav ul li a:before {
  transition: all .5s;
}
nav ul li a:hover {
  color: #020259
}



nav.stroke ul li a,
nav.fill ul li a {
  position: relative;
}
nav.stroke ul li a:after,
nav.fill ul li a:after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  content: '.';
  color: transparent;
  background: #aaa;
  height: 1px;
}
nav.stroke ul li a:hover:after {
  width: 100%;
}

nav.fill ul li a {
  transition: all 2s;
}

nav.fill ul li a:after {
  text-align: left;
  content: '.';
  margin: 0;
  opacity: 0;
}
nav.fill ul li a:hover {
  color: #fff;
  z-index: 1;
}
nav.fill ul li a:hover:after {
  z-index: -10;
  animation: fill 1s forwards;
  -webkit-animation: fill 1s forwards;
  -moz-animation: fill 1s forwards;
  opacity: 1;
}
  .icon {
    padding-left: 20px;
  }
  
  
  @media only screen and (max-width: 600px) {
         nav {
          display: in;
          position: relative;
          }
         
   
          }
header h1 {
    margin: 0;
}
header {
    margin: 0 auto;
    color:white;
    text-align: center;
    padding: 120px 20px 20px 20px; 
    font-size: 1.5em;
    
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
header a{ 
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 1);
    font-weight: 300;
    background-color:  #E3B120!important;
    border-color: #E3B120!important;
    border-radius: 5%;
    text-decoration: none;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    color: darkblue;
    display: inline-block;
}
.maindiv {
    margin: 0 auto;
    background-color: #FDE8F4;
    width: 100%;
    position: absolute;
    height: 100vh;
    text-align: center;
    font-size: 1em;
    font-family: Arial, Helvetica, sans-serif;
    color: #020259;
}
.maindiv h1, h2, h3 {
    text-decoration: underline;
}
.maindiv img{
    height: 100px ;
    border-radius: 50%;
   position: absolute;
   left: 25%;
   top: 3%;
}
.maindiv p {
    text-align: justify;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 15px;
    
}
@media only screen and (max-width: 800px) {
    .maindiv {
        height: 200%;
    }
    .maindiv img {
        left: 4%;
        top: 2%;
     }
     .maindiv p {
         font-size: 13px;
     }
     }


